.presentation {
    background-color: $color-003;
    padding: 50px 0;
    margin: 150px 0 0;
    h2 {
        margin-bottom: 50px;
        color: $color-006;
    }
    .cards {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        margin-top: 30px;
        gap: 50px;
    }
    .card {
        background-color: $color-007;
        padding: 50px;
        position: relative;
        overflow: hidden;
        z-index: 2;
        opacity: 0;
        width: 33%;
        &:hover {
            h3 {
                opacity: 0;
            }
            p {
                opacity: 1;
            }
            .img-wrapper .overlay {
                background-color: rgba($color: $color-004, $alpha: 0.8);
            }
        }
        h3 {
            color: $color-002;
            transition: all 0.4s ease-in-out;
            max-width: 80%;
        }
        p {
            opacity: 0;
            transition: all 0.4s ease-in-out;
            color: $color-002;
            text-align: justify;
        }
        .img-wrapper {
            position: absolute;
            inset: 0;
            z-index: -1;
            .overlay {
                position: absolute;
                inset: 0;
                background-color: rgba($color: $color-004, $alpha: 0.5);
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.4s ease-in-out;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}